.footer-navigation {
  margin: 0;
  padding: 0;
  width: 470px;

  @media (max-width: 769px) {
    width: 270px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}

.footer-navigation h2 {
  margin: 0;
  padding: 0;
  font-family: 'Roboto Condensed', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-default-white;
  margin-bottom: 20px;
}

.footer-navigation__list {
  margin: 0;
  padding: 0;
  /*display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 470px;
  height: 160px;*/
  columns: 220px 2;

  @include vp-1023 {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 220px;
  }

  @media (max-width: 769px) {
    display: none;
  }
}

.footer-navigation__item {
  margin: 0;
  padding: 0;
  margin-bottom: 10px;
  width: 220px;
}

.footer-navigation__link {
  color: $color-default-white;
  opacity: 0.7;
  width: 220px;
}


.footer-navigation__toggle {
  top: 0;
  right: 0;
  display: block;
  width: 30px;
  height: 30px;
  background-color: transparent;
  cursor: pointer;
  border: none;
  position: relative;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 16px;
    left: 17px;
    width: 10px;
    height: 1px;
    background-color: $color-default-white;
    z-index: 2;
  }

  &::before {
    box-shadow: none;
    top: 16px;
  }

  &::after {
    transform: rotate(-90deg);
    top: 16px;
  }

  display: none;

  @media (max-width: 769px) {
    display: block;
  }
}
