.advantages {
  margin: 0 auto;
  padding: 0;
  margin-bottom: 120px;

  @media (max-width: 769px) {
    margin-bottom: 47px;
  }
}

.advantages__list {
  margin: 0 auto;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  width: 1220px;
  justify-items: center;

  @include vp-1023 {
    width: 670px;
  }

  @media (max-width: 769px) {
    grid-template-columns: 1fr;
    max-width: 320px;
  }
}

.advantages__item {
  position: relative;
  width: 596px;
  height: 300px;
  border: 1px solid $whisper;
  padding-top: 40px;
  padding-left: 45px;

  &::before {
    content: '';
    position: absolute;
    width: 5px;
    height: 300px;
    background-color: $deep-sky-blue;
    top: -2px;
    left: 0;

    @include vp-1023 {
      height: 360px;
    }

    @media (max-width: 769px) {
      height: 341px;
      left: -1px;
    }
  }

  @include vp-1023 {
    width: 320px;
    height: 360px;
  }

  @media (max-width: 769px) {
    width: 270px;
    height: 340px;
    padding-top: 30px;
    padding-left: 30px;
  }
}

.advantages__item--confident,
.advantages__item--price {
  
  @media (max-width: 769px) {
    height: 325px;
    &::before {
        height: 325px;
        left: -1px;
    }
  }
}

.advantages__icon__hover {
  display: none;
}

.advantages__icon {
  margin: 0;
  padding: 0;
  margin-bottom: 20px;

  @media (max-width: 769px) {
    width: 30px;
    height: 30px;
    margin-bottom: 18px;
  }
}

.advantages__item--confident .advantages__icon,
.advantages__item--price .advantages__icon {
  @media (max-width: 769px) {
    margin-bottom: 15px;
  }
}

.advantages__item--confident h2,
.advantages__item--price h2 {
  @media (max-width: 769px) {
    line-height: 26px;
    width: 200px;
  }
}

.advantages__item--time h2,
.advantages--sails h2 {
  @media (max-width: 769px) {
    line-height: 14px;
  }
}

.advantages__item h2 {
  font-family: 'Roboto Condensed', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  color: $deep-sky-blue;
  margin: 0;
  padding: 0;
  margin-bottom: 15px;

  @media (max-width: 769px) {
    font-size: 20px;
    margin-bottom: 13px;
  }
}

.advantages__item p {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: $black-pearl;
  margin: 0;
  padding: 0;

  @media (max-width: 769px) {
    font-size: 14px;
    line-height: 19px;
  }
}

.advantages__item:hover {

  @media screen and (min-width: $vp-1024) {
      background-color: $deep-sky-blue;
      border-color: $deep-sky-blue;
      .advantages__icon {
        display: none;
      }

      .advantages__icon__hover {
        display: block;
        margin: 0;
        padding: 0;
        margin-bottom: 20px;
      }

      h2,
      p {
        color: $alice-blue;
      }
  }
}
